import axios from "axios";
import React, { useCallback, useEffect, useMemo, useState } from "react";
import { RangeSlider } from "rsuite";
import { Container, Grid, Header, Input, Label, Message, Statistic, Segment, Dimmer, Loader, Image } from "semantic-ui-react";
import config from "../config";
import { useDebouncedEffect } from "./use-debounced-effect";
import { Guid } from "js-guid";
import { LineChart } from "./linechart";

const emptyResult = {
  selectedIndexes: [],
  sum: 0,
  elapsedTicks: 0,
  elapsedMilliseconds: 0,
};
const fetchData = async (values, options, signal) => {
  try {
    const response = await axios.post(
      `${config.serverUrl}/genetic`,
      {
        weights: values,
        chunkGap: 3,
        ...options,
      },
      {
        headers: {
          "Content-Type": "application/json",
        },
        signal,
      }
    );
    return response.data;
  } catch (error) {
    if (error.name !== "CanceledError") {
      console.error("Error fetching data:", error);
    }
    return emptyResult;
  }
};

export const Genetic = ({ weights }) => {
  const [result, setResult] = useState(emptyResult);
  const [options, setOptions] = useState({
    populationSize: 10,
    evaluationCount: 3,
    mutationProbability: 1,
    mutationCount: 10,
  });
  const [busy, setBusy] = useState(false);

  useEffect(() => {
    const abortController = new AbortController();
    const signal = abortController.signal;
    const fetchDataAndUpdateResult = async () => {
      setBusy(true);
      setResult(emptyResult);
      const resultData = await fetchData(weights, options, signal);
      setResult(resultData);
      setBusy(false);
    };

    fetchDataAndUpdateResult();

    return () => {
      abortController.abort();
    };
  }, [weights, options]);

  const handleOptionsInputChange = useCallback((e, { name, value }) => {
    setOptions((prevValues) => ({
      ...prevValues,
      [name]: value,
    }));
  }, []);

  return (
    <Container>
      <Header> </Header>

      <Grid columns={1} stackable>
        <Grid.Row>
          <div>
            <Input
              label="populationSize ="
              name="populationSize"
              value={options.populationSize}
              onChange={handleOptionsInputChange}
              style={{ margin: "10px" }}
            />
          </div>
          <div>
            <Input
              label="evaluationCount ="
              name="evaluationCount"
              value={options.evaluationCount}
              onChange={handleOptionsInputChange}
              style={{ margin: "10px" }}
            />
          </div>
          <div>
            <Input
              label="mutationProbability ="
              name="mutationProbability"
              value={options.mutationProbability}
              onChange={handleOptionsInputChange}
              style={{ margin: "10px" }}
            />
          </div>
          <div>
            <Input
              label="mutationCount ="
              name="mutationCount"
              value={options.mutationCount}
              onChange={handleOptionsInputChange}
              style={{ margin: "10px" }}
            />
          </div>
        </Grid.Row>
      </Grid>
      <Message positive>
        <Message.Header>{(busy && "") || ""}</Message.Header>
        <p style={{ maxHeight: 300, overflow: "auto"}}> : {result.selectedIndexes.join(", ")}</p>
        <Statistic label="  " value={result.elapsedMilliseconds} />
        <Statistic label="  " value={result.sum} />
      </Message>
    </Container>
  );
};

const ParamStepper = ({ name, range, setRange, steps, stepName, setStep, min, max }) => (
  <div style={{ margin: "10px" }}>
    <Label size="large">
      {name}  {range[0]}  {range[1]}   {steps[stepName]}
    </Label>
    <RangeSlider value={range} onChange={setRange} step={steps[stepName]} progress min={min} max={max} style={{ margin: "20px" }} />
    <Input label=" =" type="number" name={stepName} value={steps[stepName]} onChange={setStep} />
  </div>
);

const emptyParamsDatagram = {
  id: Guid.newGuid(),
  sum: 0,
  elapsedTicks: 0,
  elapsedMilliseconds: 0,
  mutationCount: 0,
  mutationProbability: 0,
  evaluationCount: 0,
  populationSize: 0,
};
export const GeneticParamsAnalyzer = ({ weights }) => {
  const [iterations, setIterations] = useState(10);
  const [populationSizeRange, setPopulationSizeRange] = useState([5, 20]);
  const [evaluationCountRange, setEvaluationCountRange] = useState([50, 300]);
  const [mutationProbabilityRange, setMutationProbabilityRange] = useState([0, 1]);
  const [mutationCountRange, setmutationCountRange] = useState([1, 100]);
  const [data, setData] = useState([emptyParamsDatagram]);
  const indexedData = useMemo(() => groupData(data), [data]);
  const [steps, setSteps] = useState({
    populationStep: 5,
    evaluationCountStep: 50,
    mutationProbabilityStep: 0.2,
    mutationCountStep: 30,
  });
  const [busy, setBusy] = useState(false);

  const handleIterationsChange = useCallback((e, { value }) => {
    setIterations(value);
  }, []);
  const handleStepChange = useCallback((e, { name, value }) => {
    setSteps((steps) => ({ ...steps, [name]: value > 0 ? value : 1 }));
  }, []);

  const updateData = useCallback((datagram) => {
    setData((prev) => [...prev, datagram]);
  }, []);

  useDebouncedEffect(
    () => {
      const abortController = new AbortController();
      const signal = abortController.signal;

      const fetchAll = async () => {
        for (let populationSize = populationSizeRange[0]; populationSize <= populationSizeRange[1]; populationSize += steps.populationStep)
          for (
            let evaluationCount = evaluationCountRange[0];
            evaluationCount <= evaluationCountRange[1];
            evaluationCount += steps.evaluationCountStep
          )
            for (
              let mutationProbability = mutationProbabilityRange[0];
              mutationProbability <= mutationProbabilityRange[1];
              mutationProbability += steps.mutationProbabilityStep
            )
              for (
                let mutationCount = mutationCountRange[0];
                mutationCount <= mutationCountRange[1];
                mutationCount += steps.mutationCountStep
              ) {
                if (signal.aborted) {
                  return;
                }

                const params = {
                  populationSize,
                  evaluationCount,
                  mutationProbability,
                  mutationCount,
                };

                const tasks = [];
                for (let i = 0; i < iterations; i++) {
                  const job = async () => {
                    const data = await fetchData(weights, params, signal);

                    if (data.selectedIndexes.length) {
                      updateData({
                        id: Guid.newGuid(),
                        ...params,
                        ...data,
                      });
                    }
                  };

                  tasks.push(job());
                }

                await Promise.all(tasks);
              }

        setBusy(false);
      };

      fetchAll();

      return () => {
        setBusy(false);
        abortController.abort();
      };
    },
    () => {
      setData([]);
      setBusy(true);
    },
    [iterations, populationSizeRange, evaluationCountRange, mutationProbabilityRange, mutationCountRange, steps, weights],
    2000
  );

  return (
    <Container>
      <Header>   </Header>
      <Segment>
        <Grid columns={1} stackable>
          <Grid.Column>
            <div style={{ margin: "10px" }}>
              <Input label="  =" type="number" name="iterations" value={iterations} onChange={handleIterationsChange} />
            </div>

            <ParamStepper
              name=" "
              range={populationSizeRange}
              setRange={setPopulationSizeRange}
              steps={steps}
              stepName="populationStep"
              setStep={handleStepChange}
              min={1}
              max={100}
            />
            <ParamStepper
              name=" "
              range={evaluationCountRange}
              setRange={setEvaluationCountRange}
              steps={steps}
              stepName="evaluationCountStep"
              setStep={handleStepChange}
              min={1}
              max={1000}
            />
            <br />
            <ParamStepper
              name=" "
              range={mutationProbabilityRange}
              setRange={setMutationProbabilityRange}
              steps={steps}
              stepName="mutationProbabilityStep"
              setStep={handleStepChange}
              min={0}
              max={1}
            />
            <br />
            <ParamStepper
              name=" "
              range={mutationCountRange}
              setRange={setmutationCountRange}
              steps={steps}
              stepName="mutationCountStep"
              setStep={handleStepChange}
              min={0}
              max={1000}
            />
          </Grid.Column>
        </Grid>
      </Segment>
      <Segment>
        <Statistic label=" " value={data.length} />
        <Statistic label="    " value={indexedData.length} />
      </Segment>

      <Segment>
        <LineChart dataPoints={indexedData} />
        {busy && (
          <Dimmer active inverted>
            <Loader inverted>Loading</Loader>
          </Dimmer>
        )}
      </Segment>
    </Container>
  );
};

function groupData(data) {
  const grouped = {};

  data.forEach((item) => {
    const index = {
      mutationCount: item.mutationCount,
      mutationProbability: item.mutationProbability,
      evaluationCount: item.evaluationCount,
      populationSize: item.populationSize,
    };
    const key = Object.values(index)
      .map((x) => `${x}`)
      .join("-");

    if (!grouped[key]) {
      grouped[key] = {
        items: [],
        totalSum: 0,
        totalElapsedMilliseconds: 0,
        minSum: item.sum,
        maxSum: item.sum,
        minElapsedMilliseconds: item.elapsedMilliseconds,
        maxElapsedMilliseconds: item.elapsedMilliseconds,
        ...index,
      };
    }

    grouped[key].items.push(item);
    grouped[key].totalSum += item.sum;
    grouped[key].totalElapsedMilliseconds += item.elapsedMilliseconds;
    grouped[key].minSum = Math.min(grouped[key].minSum, item.sum);
    grouped[key].maxSum = Math.max(grouped[key].maxSum, item.sum);
    grouped[key].minElapsedMilliseconds = Math.min(grouped[key].minElapsedMilliseconds, item.elapsedMilliseconds);
    grouped[key].maxElapsedMilliseconds = Math.max(grouped[key].maxElapsedMilliseconds, item.elapsedMilliseconds);
  });

  return Object.keys(grouped).map((key) => {
    const group = grouped[key];
    const itemCount = group.items.length;

    return {
      groupKey: key,
      ...group,
      averageSum: group.totalSum / itemCount,
      averageElapsedMilliseconds: group.totalElapsedMilliseconds / itemCount,
    };
  });
}
