import React from "react";
import { Line } from "react-chartjs-2";
import { Chart as ChartJS, Tooltip, Title, LineElement, CategoryScale, LinearScale, PointElement } from "chart.js";

ChartJS.register(Tooltip, Title, LineElement, CategoryScale, LinearScale, PointElement);

// groupKey: key,
// averageSum: group.totalSum / itemCount,
// averageElapsedMilliseconds: group.totalElapsedMilliseconds / itemCount,
// minSum: group.minSum,
// maxSum: group.maxSum,
// minElapsedMilliseconds: group.minElapsedMilliseconds,
// maxElapsedMilliseconds: group.maxElapsedMilliseconds,
// items: group.items,
// itemCount,

export const LineChart = ({ dataPoints }) => {
  const data = {
    labels: dataPoints.map((point) => point.averageElapsedMilliseconds),
    datasets: [
      {
        label: "Sum over Time",
        data: dataPoints.map((point) => point.averageSum),
        fill: false,
        borderColor: "rgba(75,192,192,1)",
        tension: 0.1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      tooltip: {
        callbacks: {
          label: function (context) {
            const point = dataPoints[context.dataIndex];
            return [
              `Key: ${point.groupKey}`,
              `Average Sum : ${point.averageSum}`,
              `Average Elapsed Milliseconds: ${point.averageElapsedMilliseconds}`,
              `Mutation Count: ${point.mutationCount}`,
              `Mutation Probability: ${point.mutationProbability}`,
              `Evaluation Count: ${point.evaluationCount}`,
              `Population Size: ${point.populationSize}`,
            ];
          },
        },
      },
    },
    scales: {
      x: {
        title: {
          display: true,
          text: "    ",
        },
      },
      y: {
        title: {
          display: true,
          text: "   ",
        },
      },
    },
  };

  return <Line data={data} options={options} />;
};
